<template>
  <div class="about-container" v-loading="loading">
    <iframe :src="data.data" frameborder="0"></iframe>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState("about", ["data", "loading"]),
  },
  created() {
    this.$store.dispatch("about/fetchData");
  },
};
</script>

<style scoped lang="less">
.about-container {
  height: 100%;
  position: relative;
  iframe {
    width: 100%;
    height: 100%;
